
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.table {
	width: 100%;
	border: none;
	margin-bottom: 20px;
}
.table thead th {
  height: 22px;
	font-weight: bold;
	text-align: left;
	border: none;
	padding: 10px 15px;
	background: #d8d8d8;
	font-size: 14px;
}
.table thead tr th:first-child {
	border-radius: 8px 0 0 8px;
}
.table thead tr th:last-child {
	border-radius: 0 8px 8px 0;
}
.table tbody td {
	text-align: left;
	border: none;
	padding: 10px 15px;
	font-size: 14px;
	vertical-align: top;
}
.table tbody tr:nth-child(even){
	background: #f3f3f3;
}
.table tbody tr td:first-child {
	border-radius: 8px 0 0 8px;
}
.table tbody tr td:last-child {
	border-radius: 0 8px 8px 0;
}

.table caption {
  color: rgb(36, 52, 46);
  font-size: 22px;
  font-style: italic;
  font-weight: 800;
  margin-bottom: 20px;
}

.id_column,
.phone_column,
.city_column {
  cursor: pointer;
  transition: 0.3s;
}

.id_column:hover,
.phone_column:hover,
.city_column:hover {
  background: #17cdff2d;
}
.id_column:active,
.phone_column:active,
.city_column:active {
  background: #5f984d4b;
}

.unvisible-user {
  display: none;
  background: #61dafb;
}

.active_column {
  
  color:rgb(184, 114, 249);
}

.style-input {

  margin-left: 6px;

  width: 110px;
  
}
.pop-up_table  {
  display: inline;
  position: absolute;
  top: 0px; left: 0px;
  border: 2px solid tomato;
  background: rgb(189, 179, 179);
  border-radius: 10px;
  padding: 10px;
  z-index: 3;
  
}
.pop-up_container {
  position: absolute;
  top: 0px; left: 0px;
  margin: auto;
  position: relative;

  width: 70%;
  height: 90%;
  

  

}